$('.tabs__link').on('click', function () {
    $(this)
        .addClass('active').siblings().removeClass('active')
        .closest('div.tabs').find('div.tabs__content').removeClass('active').eq($(this).index()).addClass('active');
});


$('.slider').slick({
    slidesToShow: 3,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
    prevArrow: `<button class="slider__arrow slider__arrow--left">
    <i class="fas fa-long-arrow-alt-left"></i>
                    </button>`,
    nextArrow: `<button class="slider__arrow slider__arrow--right">
    <i class="fas fa-long-arrow-alt-right"></i>
                    </button>`,
    responsive: [
        {
            breakpoint: 1200,
            settings: {
                slidesToShow: 2,
                slidesToScroll: 1,
            }
        },
        {
            breakpoint: 768,
            settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
                arrows: false,
            }
        },
        {
            breakpoint: 480,
            settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
                arrows: false,
            }
        },
    ],
});


$(".accordion__item > .accordion__link").on("click", function () {
    if ($(this).hasClass("active")) {
        $(this).removeClass("active");
        $(this)
            .siblings(".accordion__content")
            .slideUp(200);
        $(".accordion__item > .accordion__link i")
            .removeClass("fa-minus")
            .addClass("fa-plus");
    } else {
        $(".accordion__item > .accordion__link i")
            .removeClass("fa-minus")
            .addClass("fa-plus");
        $(this)
            .find("i")
            .removeClass("fa-plus")
            .addClass("fa-minus");
        $(".accordion__item > .accordion__link").removeClass("active");
        $(this).addClass("active");
        $(".accordion__content").slideUp(200);
        $(this)
            .siblings(".accordion__content")
            .slideDown(200);
    }
});


