(function () {
    'use strict'
    let toggler = document.getElementById('toggler')
    let close = document.getElementById('close')
    let links = document.querySelectorAll('.menu__link')

    toggler.addEventListener('click', (e) => {
        e.preventDefault();
        toggler.classList.add('menu--opened')
        toggler.style.display = 'none'
    })

    close.addEventListener('click', (e) => {
        toggler.classList.remove('menu--opened')
        toggler.style.display = 'block'
    })


    links.forEach((link) => {
        link.addEventListener('click', (e) => {
            if (document.documentElement.clientWidth <= 992) {
                toggler.classList.remove('menu--opened')
                toggler.style.display = 'block'
            }
        })
    })

    let w = null;
    window.addEventListener('resize', () => {
        w = document.documentElement.clientWidth
        if (w > 992) {
            toggler.style.display = 'none'
        } else if (w <= 992 && !toggler.classList.contains('menu--opened')) { toggler.style.display = 'block' }
    })


    let sections = document.querySelectorAll('.bg');

    window.addEventListener('scroll', () => {

        toggler.classList.remove('menu--opened')
        let firstViewportSection = null;
        sections.forEach((section) => {
            let rect = section.getBoundingClientRect();

            if (!firstViewportSection) {
                if (rect.bottom > 60) {
                    firstViewportSection = section;
                }
            }

        })

        if (!(firstViewportSection.classList.contains('section--light'))) {
            toggler.style.color = '#fff';
        } else {
            toggler.style.color = 'rgb(109, 119, 73)';
        }
    })

})()
let search = document.getElementById('search')
let searchLabel = document.getElementById('searchLabel')
let togglerSearch = document.getElementById('togglerSearch')
let formSearch = document.getElementById('formSearch')
let w = null;
w = document.documentElement.clientWidth

search.addEventListener('click', (e) => {
    searchLabel.style.display = 'none'
    search.style.width = '150px'

})
search.addEventListener('blur', () => {
    if (search.value == '') {
        searchLabel.style.display = 'block'
        formSearch.classList.remove('opened')
        if (w <= 768) {
            togglerSearch.style.display = 'block'
        }
    }
    search.style.width = '80px'
})

$('input[type=search]').on('input', function () {
    clearTimeout(this.delay);
    this.delay = setTimeout(function () {
        $(this).trigger('search');
    }.bind(this), 800);
}).on('search', function () {
    if (this.value) {
        /* call ajax request here */
    }
});

togglerSearch.addEventListener('click', () => {
    formSearch.classList.add('opened')
    togglerSearch.style.display = 'none'
    searchLabel.style.display = 'none'
    $('#search').trigger("focus")
    search.setAttribute('placeholder', 'Search')
})


window.addEventListener('resize', () => {
    w = document.documentElement.clientWidth
    if (w >= 768) {
        togglerSearch.style.display = 'none'
        search.setAttribute('placeholder', '')
    } else if (w <= 768) { togglerSearch.style.display = 'block' }
})


//bottom form

let email = document.getElementById('email')
let emailLabel = document.getElementById('emailLabel')
let subscribe = document.getElementById('subscribe')


email.addEventListener('click', (e) => {
    emailLabel.style.display = 'none'
})
email.addEventListener('blur', () => {
    if (email.value == '') {
        emailLabel.style.display = 'block'
    }
})
subscribe.addEventListener('submit', () => {
    if (email.value !== '') {
        email.value = ''
    }
})